<template>
  <div class="contact-item">
    <li class="contact-group-item">
      <span class="contact-avatar" :style="setAvatar"></span>
      <span class="contact-fullname">
        {{ user.firstname }} {{ user.lastname }}
      </span>
      <div class="btn-group">
        <button class="btn-more-info" @click="checkUser(user)">
          <i class="fas fa-info-circle"></i>
        </button>
        <button
          class="btn-delete-contact"
          @click="checkId(user.id, user.fullname)"
        >
          <i class="fas fa-trash-alt"></i>
        </button>
      </div>
    </li>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ContactItem",
  components: {},
  props: {
    user: {
      type: Object,
      default: () => ({})
    }
  },
  data: () => ({}),
  computed: {
    ...mapGetters(["showConfirmResult"]),
    setAvatar() {
      return this.user.gender === "male"
        ? {
            "background-image": `url("https://html5css.ru/w3css/img_avatar3.png")`
          }
        : {
            "background-image": `url("https://html5css.ru/howto/img_avatar2.png")`
          };
    }
  },
  methods: {
    ...mapActions([
      "setCurrentUser",
      "showUserInfo",
      "showModal",
      "confirmation"
    ]),
    checkId() {
      this.showModal("true");
      this.confirmation(this.user);
    },
    checkUser(user) {
      this.setCurrentUser(user);
      this.showUserInfo(true);
    }
  }
};
</script>

