<template>
  <div class="user-card">
    <template v-if="showFieldModal">
      <div class="modal-wrap">
        <ConfirmWindowForFields />
      </div>
    </template>
    <div class="card-header">
      <div class="card-logo" :style="setAvatar"></div>
      <div class="card-title">{{ fullName }}</div>
    </div>
    <div class="user-descr">
      <FieldItem
        v-for="(value, keyInObj) in showCurrentUser"
        :key="keyInObj"
        :keys="keyInObj"
        :values="value"
        class="field-wrap"
        @deleteField="onDeleteKey"
        @saveValue="onSaveValue"
        @canceled="onCancelled"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import FieldItem from "./FieldItem";
import ConfirmWindowForFields from "./ConfirmWindowForFields";
export default {
  name: "UserCard",
  components: {
    FieldItem,
    ConfirmWindowForFields
  },
  data: () => ({
    fieldValue: ""
  }),
  methods: {
    ...mapActions([
      "showFieldModalWindow",
      "editField",
      "removeField",
      "confirmation"
    ]),
    onDeleteKey(key) {
      this.confirmation({
        msg: "Do you wont delete this field?",
        action: "deleteField",
        key: key
      });
    },
    onSaveValue(key, newValue) {
      const newField = [key, newValue, this.showCurrentUser.id];
      this.confirmation({
        msg: "Do you save new value?",
        action: "saveNewChanges",
        key: newField
      });
    },
    onCancelled(key, value) {
      const prevRes = [key, value, this.showCurrentUser.id];
      this.confirmation({
        msg: "Do you want to cancel action?",
        action: "cancel",
        key: prevRes
      });
    }
  },
  computed: {
    ...mapGetters(["showCurrentUser", "showFieldModal"]),
    setAvatar() {
      return this.showCurrentUser.gender === "male"
        ? {
            "background-image": `url("https://html5css.ru/w3css/img_avatar3.png")`
          }
        : {
            "background-image": `url("https://html5css.ru/howto/img_avatar2.png")`
          };
    },
    fullName() {
      return `${this.showCurrentUser.firstname} ${this.showCurrentUser.lastname} `;
    }
  }
};
</script>

