<template>
  <div class="contact-list-wrap">
    <ContactForm v-if="toggleForm" @onCloseModal="closeForm" />
    <div class="contact-group-controll">
      <button class="add-contact" @click="showForm">
        <i class="fas fa-user-plus"></i>
      </button>
    </div>
    <div class="contacts-group">
      <ul class="contacts-group-wrap">
        <template v-if="listLength">
          <ContactItem v-for="(value, key) in list" :key="key" :user="value" />
        </template>
        <template v-else>
          <h1 class="warning">List is empty!</h1>
        </template>
      </ul>
    </div>
  </div>
</template>

<script>
import ContactItem from "./ContactItem";
import ContactForm from "./ContactForm";

export default {
  name: "ContactList",
  components: {
    ContactItem,
    ContactForm
  },
  props: {
    list: {
      type: Object,
      default: () => ({})
    }
  },
  data: () => ({
    toggleForm: false
  }),
  computed: {
    listLength() {
      return Object.keys(this.list).length;
    }
  },
  methods: {
    showForm() {
      this.toggleForm = true;
    },
    closeForm({ result }) {
      this.toggleForm = result;
    }
  }
};
</script>

