<template>
  <div class="confirm-window" @click="checkSelected">
    <h2 class="confirm-title">
      Are you sure delete this <span>{{ fullName }} </span>contact?
    </h2>
    <div class="confirm-controll">
      <button class="btn-cancell">Cancell</button>
      <button class="btn-ok">Confirm</button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "ConfirmWindow",
  methods: {
    ...mapActions(["showModal", "confirmation", "removeContact"]),
    checkSelected(e) {
      if (e.target.className === "btn-ok") {
        const { id } = this.showConfirmResult;
        this.removeContact(id);
        this.showModal(false);
      } else if (e.target.className === "btn-cancell") {
        this.showModal(false);
      }
    }
  },
  computed: {
    ...mapGetters(["showConfirmResult"]),
    fullName() {
      return ` ${this.showConfirmResult.firstname} ${this.showConfirmResult.lastname} `;
    }
  }
};
</script>

