<template>
  <div class="form-wrap" @click="closeModal($event)">
    <form class="contact-form" ref="clear" @submit.prevent="onSubmit">
      <div class="logo-card">
        <i class="far fa-address-card"></i>
      </div>
      <div class="first-name-wrap">
        <label for="firstname">
          First name:
          <input
            type="text"
            id="firstname"
            v-model.trim="firstname"
            :class="{ invalid: $v.firstname.$dirty && !$v.firstname.required }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.firstname.$dirty && !$v.firstname.required"
        >
          Plese, enter your first name
        </small>
      </div>

      <div class="last-name-wrap">
        <label for="lastname">
          Last name:
          <input
            type="text"
            id="lastname"
            v-model.trim="lastname"
            :class="{ invalid: $v.lastname.$dirty && !$v.lastname.required }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.lastname.$dirty && !$v.lastname.required"
        >
          Please, enter your last name
        </small>
      </div>

      <div class="phone-wrap">
        <label for="phone">
          Phone:
          <input
            type="tel"
            id="phone"
            v-model.trim="phone"
            :class="{ invalid: $v.phone.$dirty && !$v.phone.required }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.phone.$dirty && !$v.phone.required"
        >
          Please, provide number (+38)
        </small>
      </div>
      <div class="skype-wrap">
        <label for="skype">
          Skype:
          <input
            type="text"
            id="skype"
            v-model.trim="skype"
            :class="{ invalid: $v.skype.$dirty && !$v.skype.required }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.skype.$dirty && !$v.skype.required"
        >
          Please, provide nickname
        </small>
      </div>
      <div class="date-birth-wrap">
        <label for="date-birth">
          Date birth:
          <input
            type="date"
            id="datebirth"
            v-model.trim="datebirth"
            :class="{
              invalid: $v.datebirth.$dirty && !$v.datebirth.required
            }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.datebirth.$dirty && !$v.datebirth.required"
        >
          Choose date
        </small>
      </div>

      <div class="email-wrap">
        <label for="email">
          E-mail:
          <input
            type="text"
            id="email"
            v-model.trim="email"
            :class="{
              invalid: $v.email.$dirty && !$v.email.required
            }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.email.$dirty && !$v.email.required"
        >
          Please, provide email example@mail.com
        </small>
      </div>
      <div class="country-wrap">
        <label for="country">
          Country:
          <input
            type="text"
            id="country"
            v-model.trim="country"
            :class="{
              invalid: $v.country.$dirty && !$v.country.required
            }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.country.$dirty && !$v.country.required"
        >
          Please, provide country
        </small>
      </div>
      <div class="city-wrap">
        <label for="city">
          City:
          <input
            type="text"
            id="city"
            v-model.trim="city"
            :class="{
              invalid: $v.city.$dirty && !$v.city.required
            }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.city.$dirty && !$v.city.required"
        >
          Please, provide city
        </small>
      </div>
      <div class="work-wrap">
        <label for="work">
          Work:
          <input
            type="text"
            id="work"
            v-model.trim="work"
            :class="{
              invalid: $v.work.$dirty && !$v.work.required
            }"
          />
        </label>
        <small
          class="helper-text invalid"
          v-if="$v.work.$dirty && !$v.work.required"
        >
          Please, provide value
        </small>
      </div>

      <div class="form-controls">
        <button class="btn-submit" type="submit">
          <i class="fas fa-plus-circle"></i>
          Create
        </button>
        <button class="btn-close" type="button">
          <i class="fas fa-times"></i>
          close
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import { email, required, minLength } from "vuelidate/lib/validators";
import { mapActions } from "vuex";

export default {
  name: "ContactForm",
  data: () => ({
    firstname: "",
    lastname: "",
    fullname: "",
    phone: "",
    skype: "",
    datebirth: "",
    email: "",
    country: "",
    city: "",
    work: "",
    id: "",
    submitStatus: null
  }),
  validations: {
    firstname: { required, minLength: minLength(6) },
    lastname: { required, minLength: minLength(6) },
    phone: { required, minLength: minLength(13) },
    skype: { required },
    datebirth: { required },
    email: { email, required },
    country: { required, minLength: minLength(3) },
    city: { required, minLength: minLength(3) },
    work: { required }
  },
  methods: {
    ...mapActions(["addNewContact", "showFieldModal"]),
    onSubmit() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.submitStatus = "ERROR";
      }

      const user = {
        firstname: this.firstname,
        lastname: this.lastname,
        phone: this.phone,
        skype: this.skype,
        datebirth: this.datebirth,
        email: this.email,
        country: this.country,
        city: this.city,
        work: this.work,
        id: String(Math.random())
      };
      this.addNewContact(user);
      this.$refs.clear.reset();
    },
    closeModal(e) {
      if (
        e.target.className === "form-wrap" ||
        e.target.className === "btn-close"
      ) {
        this.$emit("onCloseModal", { result: false });
      }
    }
  }
};
</script>

