  bottom: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 2s ease;
}
.contact-form {
  display: flex;
  flex-direction: column;
  width: 700px;
  padding: 15px 30px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  color: #fff;
  position: relative;
}
.contact-form div {
  margin-top: 5px;
}
.contact-form div label {
  font-size: 12px;
}
.contact-form div input {
  width: 100%;
  margin-top: 5px;
  font-size: 18px;
  background: none;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  color: #fff;
  transition: all 0.5s ease;
}
.contact-form div input:focus {
  padding: 10px 0;
  font-size: 15px;
}
.contact-form div input.invalid {
  border-bottom: 1px solid red;
}
.form-controls {
  padding: 20px 0 0 0;
  display: flex;
  justify-content: space-around;
}
.btn-submit,
.btn-close {
  background-color: #e65100;
  padding: 10px;
  color: #fff;
  outline: none;
  border: none;
  width: 120px;
  border-radius: 5px;
  font-weight: 100;
  font-size: 18px;
  text-transform: uppercase;
  display: inline-block;
  transition: all 1s ease;
}
.btn-close {
  background-color: #1b5e20;
}
.btn-submit:hover {
  background-color: #ff8a65;
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.7);
}
.btn-close:hover {
  background-color: #81c784;
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.7);
}

.fa-plus-circle .fa-times {
  margin-right: 5px;
}
.logo-card {
  width: 70px;
  height: 70px;
  border-radius: 100%;
  border: 2px solid #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
  margin: 0 auto;
  background-color: black;
}
.fa-address-card {
  font-size: 40px;
}
small {
  color: red;
  margin-top: 10px;
  display: inline-block;
  font-size: 12px;
}
</style>
