<template>
  <div class="confirm-window" @click="checkSelected">
    <h2 class="confirm-title">
      {{ this.showConfirmResult.msg || "" }}
    </h2>
    <div class="confirm-controll">
      <button class="btn-cancell">Cancell</button>
      <button class="btn-ok">Confirm</button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "ConfirmWindow",
  props: {},
  data: () => ({}),
  computed: {
    ...mapGetters(["showConfirmResult"])
  },
  methods: {
    ...mapActions([
      "showFieldModalWindow",
      "removeField",
      "editField",
      "canceledEdit"
    ]),
    checkSelected(e) {
      const { action, key } = this.showConfirmResult;
      if (e.target.className === "btn-ok") {
        if (action === "deleteField") {
          this.removeField(key);
        } else if (action === "saveNewChanges") {
          this.editField(key);
        } else if (action === "cancel") {
          this.canceledEdit(key);
        }
      }
      this.showFieldModalWindow(false);
    }
  }
};
</script>

