.confirm-window {
  width: 700px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.9);
  padding: 30px;
  z-index: 99999999;
}
.confirm-window h2 {
  color: black;
  text-align: center;
  font-size: 30px;
  font-weight: 100;
  margin-top: 20px;
}
.confirm-controll {
  margin-top: 60px;
  display: flex;
  flex-direction: row-reverse;
}
.btn-ok,
.btn-cancell {
  padding: 10px 15px;
  border-radius: 5px;
  outline: none;
  border: none;
  margin-left: 10px;
  background-color: #37474f;
  color: #ffffff;
  transition: all 0.6s ease;
}
.btn-ok:hover,
.btn-cancell:hover {
  background-color: #b0bec5;
}
