import Vue from "vue";
import Vuex from "vuex";
import users from "../mock/mock";
import mutations from "./mutations";

const {
  REMOVE_CONTACT,
  ADD_CONTACT,
  CURRENT_USER,
  TOGGLE_INFO,
  REMOVE_FIELD,
  EDIT_FIELD,
  CANCELLED_EDIT,
  TOGGLE_MODAL,
  CONFIRM_RESULT,
  TOGGLE_FIELD_MODAL
} = mutations;

Vue.use(Vuex);

export default new Vuex.Store({
  namespaced: true,
  state: {
    objOfUsers: users,
    showContactInfo: false,
    currentUser: {},
    showModal: false,
    res: null,
    isShowFieldModal: false
  },
  getters: {
    showUsers: ({ objOfUsers }) => objOfUsers,
    showInfoPage: ({ showContactInfo }) => showContactInfo,
    showCurrentUser: ({ currentUser }) => currentUser,
    showModalWindow: ({ showModal }) => showModal,
    showConfirmResult: ({ res }) => res,
    showFieldModal: ({ isShowFieldModal }) => isShowFieldModal
  },
  mutations: {
    [ADD_CONTACT](state, user) {
      Vue.set(state.objOfUsers, user.id, user);
    },
    [REMOVE_CONTACT](state, id) {
      Vue.delete(state.objOfUsers, id);
    },
    [CURRENT_USER](state, user) {
      state.currentUser = user;
    },
    [TOGGLE_INFO](state, value) {
      state.showContactInfo = value;
    },
    [REMOVE_FIELD](state, field) {
      Vue.delete(state.currentUser, field);
    },
    [EDIT_FIELD](state, newField) {
      const [key, value, id] = newField;
      Vue.set(state.currentUser, key, value);
      Vue.set(state.objOfUsers, id, state.currentUser);
    },
    [CANCELLED_EDIT](state, prevRes) {
      const [key, value, id] = prevRes;
      Vue.set(state.currentUser, key, value);
      Vue.set(state.objOfUsers, id, state.currentUser);
    },
    [TOGGLE_MODAL](state, toggleRes) {
      state.showModal = toggleRes;
    },
    [CONFIRM_RESULT](state, answer) {
      state.res = answer;
    },
    [TOGGLE_FIELD_MODAL](state, toggle) {
      state.isShowFieldModal = toggle;
    }
  },
  actions: {
    addNewContact({ commit }, user) {
      commit("ADD_CONTACT", user);
    },
    removeContact({ commit }, id) {
      commit("REMOVE_CONTACT", id);
    },
    setCurrentUser({ commit }, user) {
      commit("CURRENT_USER", user);
    },
    showUserInfo({ commit }, val) {
      commit("TOGGLE_INFO", val);
    },
    removeField({ commit }, field) {
      commit("REMOVE_FIELD", field);
    },
    editField({ commit }, newField) {
      commit("EDIT_FIELD", newField);
    },
    canceledEdit({ commit }, prevRes) {
      commit("CANCELLED_EDIT", prevRes);
    },
    showModal({ commit }, toggleRes) {
      commit("TOGGLE_MODAL", toggleRes);
    },
    confirmation({ commit }, res) {
      commit("CONFIRM_RESULT", res);
    },
    showFieldModalWindow({ commit }, toggle) {
      commit("TOGGLE_FIELD_MODAL", toggle);
    }
  },
  modules: {}
});
