<template>
  <div class="field-wrap">
    <div class="field">
      <h3 class="field-key">{{ this.keys }}:</h3>
      <template v-if="!isEditing">
        <h3 class="field-value">{{ this.values }}</h3>
        <div class="btn-group">
          <button @click="editFieldEdit(values)" class="btn-edit">
            <i class="fas fa-edit"></i>
          </button>
          <button @click="deleteField(keys)" class="btn-del">
            <i class="far fa-trash-alt"></i>
          </button>
        </div>
      </template>
      <template v-if="isEditing">
        <div class="edit-group">
          <input type="text" v-model="newValue" class="edit-input" />
          <button @click="saveNewValue(keys, newValue)" class="btn-save">
            Save
          </button>
          <button @click="cancel(keys, values)" class="btn-cancel">
            Cancel
          </button>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "FieldItem",
  components: {},
  props: {
    keys: {
      required: true
    },
    values: {
      required: true
    }
  },
  data: () => ({
    isEditing: false,
    newValue: ""
  }),
  methods: {
    ...mapActions(["showFieldModalWindow"]),
    editFieldEdit(value) {
      this.isEditing = !this.isEditing;
      this.newValue = value;
    },
    deleteField(key) {
      this.$emit("deleteField", key);
      this.showFieldModalWindow(true);
    },
    saveNewValue(key, value) {
      this.$emit("saveValue", key, value);
      this.isEditing = !this.isEditing;
      this.showFieldModalWindow(true);
    },
    cancel(key, value) {
      this.$emit("canceled", key, value);
      this.isEditing = !this.isEditing;
      this.showFieldModalWindow(true);
    }
  },
  computed: {
    ...mapGetters(["showFieldModal"])
  }
};
</script>

